export function getDecoded(msg) {
    if (!msg)
        return null;
    const mc = msg.message_content;
    if (isRecord(mc)) {
        const d = mc.decoded;
        return isRecord(d) ? d : null;
    }
    return null;
}
export function extractOpFromBody(msg) {
    if (!msg)
        return null;
    const decoded = getDecoded(msg);
    if (isRecord(decoded)) {
        const t = decoded['@type'];
        if (typeof t === 'string' && t.length > 0)
            return t;
        const val = decoded['value'];
        if (isRecord(val) && typeof val['@type'] === 'string')
            return val['@type'];
    }
    return null;
}
export function matchOpWithMap(op, types, mapping) {
    if (!op)
        return '';
    const normalized = mapping[op] ?? op;
    return types.includes(normalized) ? normalized : '';
}
function isRecord(v) {
    return typeof v === 'object' && v !== null;
}
