export class BasicHandler {
    _notifyHandler;
    constructor(notify) {
        this._notifyHandler = notify;
    }
    async notify(event) {
        if (this._notifyHandler) {
            return await this._notifyHandler(event);
        }
        return Promise.resolve();
    }
}
