        const m = metadata[master];
        name = m['name'] || '';
        symbol = m['symbol'] || '';
        const extra = m['extra'];
        const dec = extra?.['decimals'];
        decimals = typeof dec === 'string' ? parseInt(dec, 10) : 0;
        image =
            m['image'] ||
                extra?.['_image_small'] ||
                extra?.['_image_medium'] ||
                extra?.['_image_big'];
